<template>
  <div class="todo-container">
    <h1>Todo</h1>
    <!-- add todo -->
    <div class="add-todo">
      <label for="todo">add a new todo</label>
      <input type="text" id="tod" v-model="content" @keyup.enter="postTodo" />
      <button class="add" @click="postTodo">Add!</button>
    </div>
    <!-- getting todos -->
    <div
      class="todos"
      v-for="todo in todos"
      :key="todo._id"
      :style="{ border: '2px solid green', margin: '5px', padding: '5px' }"
    >
      <span>{{ todo.date }}</span>
      <p>{{ todo.content }}</p>
      <button class="delete" @click="deleteTodo(todo._id)">delete</button>
    </div>
  </div>
</template>

<script>
import todoServices from "@/todoServices";

export default {
  name: "TodoComponent",
  data() {
    return {
      todos: [],
      error: "",
      content: "",
    };
  },
  async created() {
    // getting data from db after creating page
    // console.log(await todoServices.getTodos());
    this.todos = await todoServices.getTodos();
  },
  methods: {
    async postTodo() {
      // it will take its param from (this.)
      await todoServices.postTodo(this.content);

      // clear the input
      this.content = "";

      // add the posted todo to this.todos
      this.todos = await todoServices.getTodos();
    },
    async deleteTodo(id) {
      await todoServices.deleteTodo(id);

      this.todos = await todoServices.getTodos();
    },
  },
};
</script>

